import { useStaticQuery, graphql, Link } from 'gatsby'
import { GatsbyImage } from 'gatsby-plugin-image'
import React, { useState, useEffect } from 'react'
import { Heading } from './style' // Global styled-components
import { Container, Row, Col } from 'reactstrap'
import { BlogEntry } from './Blog/style'
import styled from 'styled-components'
import { motion } from 'motion/react'
import { fadeInUpVariants } from '../animations'
import { ANIMATION_CONFIG, TIMING } from '../animations/config'
import { useInViewAnimation } from '../animations/hooks/useInViewAnimation'

const Image = styled(GatsbyImage)`
    border-radius: 5px;
    width: 75%;
    margin: 0 auto;
`;

// Animated blog entry component
const AnimatedBlogEntry = ({ children, index }: { children: React.ReactNode; index: number }) => {
    const [isMounted, setIsMounted] = useState(false);

    // Only run animation hook on client side
    const { ref, isInView } = useInViewAnimation({
        once: true,
        rootMargin: ANIMATION_CONFIG.rootMargin,
    });

    useEffect(() => {
        setIsMounted(true);
    }, []);

    // During SSR, just render without animation
    if (!isMounted) {
        return <BlogEntry>{children}</BlogEntry>;
    }

    return (
        <BlogEntry ref={ref as any}>
            <motion.div
                initial="hidden"
                animate={isInView ? "visible" : "hidden"}
                custom={{ delay: index * 0.1, distance: TIMING.primaryUnit.distance }}
                variants={fadeInUpVariants}
            >
                {children}
            </motion.div>
        </BlogEntry>
    );
};


export const Blog = (props) => {
    const [isMounted, setIsMounted] = useState(false);

    const data = useStaticQuery(
        graphql`
            query {
                allMdx(
                    sort: {frontmatter: {date: DESC}}
                    filter: { frontmatter: { published: { eq: true } } }
                ) {
                    nodes {
                        id
                        excerpt(pruneLength: 250)
                        frontmatter {
                            title
                            date(formatString: "YYYY MMMM Do")
                        }
                        fields {
                            slug
                        }
                    }
                }
            }
        `
    );

    // Use the optimized hook for header viewport detection
    const { ref: headerRef, isInView: isHeaderVisible } = useInViewAnimation({
        once: true,
        rootMargin: ANIMATION_CONFIG.rootMargin,
    });

    useEffect(() => {
        setIsMounted(true);
    }, []);


    return (
        <BlogSection name="Blog">
            <Container fluid={true} className="">
                <Row noGutters className="justify-content-center pb-5">
                    <Col md={12} className="heading-section text-center">
                        {isMounted ? (
                            <motion.div
                                ref={headerRef}
                                initial="hidden"
                                animate={isHeaderVisible ? "visible" : "hidden"}
                                custom={{ delay: 0, distance: TIMING.sectionHeader.distance }}
                                variants={fadeInUpVariants}
                            >
                                <Heading className="mb-4 mt-5">Blog</Heading>
                                <p>Coding, reflecting, evolving.</p>
                            </motion.div>
                        ) : (
                            <div>
                                <Heading className="mb-4 mt-5">Blog</Heading>
                                <p>Coding, reflecting, evolving.</p>
                            </div>
                        )}
                    </Col>
                </Row>
                <Row>
                    {data.allMdx.nodes.map(
                        ({ id, excerpt, frontmatter, fields }, index) => (
                            <AnimatedBlogEntry key={id} index={index}>
                                <Link to={fields.slug}>
                                    {/* TODO: Implement proper cover image handling - cover is currently a string path */}
                                    {/* {frontmatter.cover ? (
                                        <Image
                                            image={frontmatter.cover.childImageSharp.gatsbyImageData}
                                            className='mx-auto'
                                        />
                                    ) : null} */}
                                    <h1>{frontmatter.title}</h1>
                                    <p>{frontmatter.date}</p>
                                    <p class="excerpt">{excerpt}</p>
                                </Link>
                            </AnimatedBlogEntry>
                        )
                    )}
                </Row>
            </Container>
        </BlogSection>
    );
}

